* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.country-list{
  list-style: none;
}
.item{
  display: flex;
  align-items: center;
}
.text{
  margin-left: 15px;
}
.flag{
  margin-right: 15px;
}
.big{
  font-weight: bold;
  margin-right: 10px;
}
.country-info{
  margin-left: 50px;
  
}